.header {
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
  height: 60px;
  background-color: #2c3e50;
  display: flex;
  align-items: center;
  justify-content: space-between;
  padding: 0 20px;
  z-index: 1000;
  overflow: hidden;
}

.noise {
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  background-image: url('data:image/png;base64,');
  opacity: 0.1;
  z-index: -1;
}

.nav {
  display: flex;
  gap: 20px;
}

.navButton {
  color: white;
  text-decoration: none;
  font-weight: bold;
  font-size: 18px;
  padding: 10px 15px;
  border-radius: 5px;
  transition: background-color 0.3s ease;
}

.navButton:hover {
  background-color: rgba(255, 255, 255, 0.1);
}

.searchForm {
  display: flex;
  align-items: center;
}

.searchInput {
  padding: 8px 12px;
  border: none;
  border-radius: 20px 0 0 20px;
  font-size: 16px;
  outline: none;
  width: 200px;
}

.searchButton {
  padding: 8px 12px;
  border: none;
  background-color: #3498db;
  color: white;
  font-weight: bold;
  border-radius: 0 20px 20px 0;
  cursor: pointer;
  transition: background-color 0.3s ease;
}

.searchButton:hover {
  background-color: #2980b9;
}

@media (max-width: 768px) {
  .header {
    flex-direction: column;
    height: auto;
    padding: 10px;
  }

  .nav {
    margin-bottom: 10px;
  }

  .searchForm {
    width: 100%;
  }

  .searchInput {
    width: 100%;
  }
}
